<template>
  <div class="card vue-avatar-cropper text-center">
    <div class="card-body">
        <v-avatar color="grey" size="164" >
            <v-img :src="user.avatar" class="card-img avatar"></v-img>
        </v-avatar>
        <div class="card-img-overlay">
            <v-btn id="pick-avatar" small color="primary">
                <v-icon left>mdi-cloud-upload</v-icon>
            Update
            </v-btn>
        </div>
    </div>
    <v-alert v-if="message" v-html="message" :type="type">

    </v-alert>
    <avatar-cropper
        @uploading="handleUploading"
        @uploaded="handleUploaded"
        @completed="handleCompleted"
        @error="handlerError"
        :labels =labels
        trigger="#pick-avatar"
        upload-url="https://demo.overtrue.me/upload.php" />
  </div>
</template>

<script>
import AvatarCropper from "vue-avatar-cropper";

export default {
  components: { AvatarCropper },
  props : {
  },
  data() {
    return {
      message: null,
      type : null,
      user: {
        id: 1, 
        avatar: "https://cdn.vuetifyjs.com/images/profiles/marcus.jpg"
      },
      labels: { "submit": 'Submit', "cancel": 'Cancel' }
        
    }
  },
  methods: {
      
    handleUploading(form, xhr) {
      this.message = "uploading...";
      this.type = "info"
      console.log(form, xhr)
    },
    handleUploaded(response) {
      if (response.status == "success") {
        this.type = "success"
        this.user.avatar = response.url;
        // Maybe you need call vuex action to
        // update user avatar, for example:
        // this.$dispatch('updateUser', {avatar: response.url})
        this.message = "user avatar updated.";
      }
    },
    handleCompleted(response, form, xhr) {
      this.type = "success"
      this.message = "upload completed.";
       console.log(response, form, xhr)
    },
    handlerError(message, type, xhr) {
      this.type = "error"
      this.message = "Oops! Something went wrong...";
      console.log(message, type, xhr)
    }
  }
};
</script>

<style>
.vue-avatar-cropper {
  max-width: 18em;
  margin: 0 auto;
}
.avatar {
  width: 160px;
  border-radius: 6px;
  display: block;
}
.card-img-overlay {
  display: none;
  transition: all 0.5s;
  margin-bottom: -3.5vh;
}
.card-img-overlay button {
  margin-top: -10vh;
}
.card:hover .card-img-overlay {
  display: block;
}
</style>
